@import '../../../common/common.scss';

.am-wrapper {
  display: flex;
  flex-direction: column;
  height: 60vh;
  width: 50%;
  font-family: 'Nexa-Heavy';

  .content-wrapper {
    background: radial-gradient(circle at 7.5% 24%, rgba(237, 161, 193, 0.8) 0%, rgba(250, 178, 172, 0.8) 25.5%, rgba(190, 228, 210, 0.8) 62.3%, rgba(215, 248, 247, 0.8) 93.8%);
    border-radius: 20px;
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(4.9px);
    -webkit-backdrop-filter: blur(4.9px);
    padding: 20px;
    transition: transform 0.3s ease;
    font-size: 20px;
    display: flex;
    align-items: center;
    gap: 10px;

    p {
      margin: 0;
      transition: color 0.3s ease;
    }

    img {
      width: 100%;
      @media screen and (max-width: 768px) {
        width: 50%;
      }
      max-height: 100px;
      max-width: 100px;
      border-radius: 20px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
      transition: transform 0.3s ease, box-shadow 0.3s ease;
      padding: 10px;
    }
  }

  .content-wrapper:hover {
    transform: translateY(-5px);
    background: linear-gradient(111.1deg, rgba(0, 40, 70, 0.8) -4.8%, rgba(255, 115, 115, 0.8) 82.7%, rgba(255, 175, 123, 0.8) 97.2%);

    img {
      transform: scale(1.05);
      box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
    }

    p {
      color: #fff;
    }
  }
}

@media screen and (max-width: 768px) {
  .am-wrapper {
    width: 70%;
    height: 90vh;
  }

  .content-wrapper {
    flex-direction: column-reverse;
    align-items: flex-start;
    gap: 20px;
    padding: 15px;
    p{
        font-size: 15px;
    }
  }
}
